export const initialState = {
  basket: [],
  user: null, //when the app is load the user by default is null
};
//selector
export const getBasketTotal = (basket) =>
  basket?.reduce((amount, item) => item.price + amount, 0);

const reducer = (state, action) => {
  switch (action.type) {
    case "ADD_TO_BASKET":
      return {
        ...state,
        basket: [...state.basket, action.item], // this will push item in to our basket
      };
    case "REMOVE_FROM_BASKET": // if we do return {...state,basket: state.basket.filter(item => item.id !== action.id )} this will remove products with the same
      const index = state.basket.findIndex(
        (basketItem) => basketItem.id === action.id
      );
      let newBasket = [...state.basket];

      if (index >= 0) {
        newBasket.splice(index, 1); //cut out the element with the index value
      } else {
        console.warn(`not found(id: ${action.id}) as its not in basket`);
      }
      return {
        ...state,
        basket: newBasket,
      };
    case "SET_USER":
      return {
        ...state,
        user: action.user,
      };

    default:
      return state;
  }
};
export default reducer;
